import styles from "../styles/ToggleButton.module.css"
import { AppContext } from "../context/App";
import { useContext } from "react";

const ToggleButton = ({ increase, decrease}) => {
    let age = useContext(AppContext)

    let {toggleButton, toggleButton__button, "toggleButton__button--green":green} = styles
    let greenButton = `${toggleButton__button} ${green}`
    
  return (
    <div className={toggleButton}>
        <button className={toggleButton__button} onClick={()=>increase()}>
            +
        </button>
        <span>
            { age }
        </span>
        <button className={greenButton} onClick={()=>decrease()}>
            -
        </button>
    </div>)
}

export default ToggleButton

