import PropTypes from 'prop-types';
import styles from "../styles/Age.module.css"

const Age = ({age}) => {
  return (
    <div className={styles.age}>
        <h1>
            You are {age} years old
        </h1>
    </div>
  )
}
export default Age

Age.defaultProps = {
    age:29
}
Age.propTypes = {
    age:PropTypes.number
}
