import Age from "./components/Age"
import YOB from "./components/YOB"
import MainWrapper from "./layout/MainWrapper"
import ToggleButton from "./components/ToggleButton"
import { useState} from "react"
import { AppWrapper } from "./context/App"

const App = () => {
  let [age, setAge] = useState(29)
  let increaseAge = ()=>{
    setAge((a)=>a + 1)
  }
  let decreaseAge = ()=>{
    setAge((a)=>a - 1)
  }
  return (
    <div>
      <AppWrapper data={age}>
          <MainWrapper>
                <Age age={age}></Age>
                <YOB></YOB> 
          </MainWrapper>
          <ToggleButton increase={increaseAge} decrease={decreaseAge}></ToggleButton>
        </AppWrapper>
    </div>
  )
}

export default App