import React, { useContext } from 'react'
import PropTypes from 'prop-types';
import styles from "../styles/YOB.module.css"
import { AppContext } from "../context/App";

const YOB = () => {
  let age = useContext(AppContext)
  return (
    <div className={styles.YOB}>
    <h1>You were born in the year {2022 - age} </h1>
  </div>
  )
}

export default YOB

YOB.defaultProps = {
    age:29
}
YOB.propTypes = {
    age:PropTypes.number
}


